import React from 'react';
import SocialBar from './components/SocialBar'
import profile from './assets/profile.png'
import hello from './assets/hello.png'
import './App.css';

function App() {
  return (
    <div className="app">
      <div className="bioContainer hide-if-small margin">
        <img style={{width: 150, height: 125, marginBottom: '5vh' }} src={hello} alt="" />
        <p>
          I'm Chris, a Berlin-based software developer that's passionate about building unique web and mobile applications.
        </p>
        <br />
        <p>
          I'm currently accepting interview invitations for full-time frontend positions.
          {/* You can find my CV here, and my details across the page. */}
        </p>
      </div>
      <div className="socialContainer">
        <img className="profile" src={profile} alt="" />
        <SocialBar />
      </div>
    </div>
  );
}

export default App;
